.Card {
    margin: var(--margin);
    padding: var(--padding);
    min-height: 16rem;
    min-width: 16rem;
    background-color: var(--surface-color);
    color: var(--on-surface-color);
    border-radius: var(--radius);
    text-align: left;
}

.Card:hover {
    background-color: var(--highlight-color);
    box-shadow: var(--box-shadow);
}
