.Page {
    margin: var(--margin);
    background-color: var(--background-color);
}
.PageContent {
    display: block;
    margin: 0 auto;
    padding: var(--padding);
    min-height: 80vh;
    max-width: 64rem;
    color: var(--on-surface-color);
    border-radius: var(--radius);
    box-shadow: var(--box-shadow);
    z-index: 10;
}
