.NavigationBar {
    display: inline-flex;
    align-items: center;
    width: 100vw;
    background-color: var(--surface-color);
    color: var(--on-surface-color);
    box-shadow: var(--box-shadow);
    z-index: 100;
}
.NavigationItems {
    margin: 0 auto 0 0;
    padding: var(--padding);
    display: inline-flex;
}
.NavigationItem {
    margin: 0 var(--margin);
}
